/*Pixels to rems*/ /* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --orange: hsl(35, 77%, 62%);
  --Red: hsl(5, 85%, 63%);
  --White: hsl(36, 100%, 99%);
  --Grayish-blue: hsl(233, 8%, 79%);
  --Dark-blue: hsl(236, 13%, 42%);
  --Very-dark-blue: hsl(240, 100%, 5%);
}

body {
  font-family: "Inter", sans-serif;
}

@media (max-width: 56.25em) {
  body {
    background: #666;
  }
}

.header {
  display: flex;
  width: 100%;
  align-items: center;
  padding-top: 3.75rem;
  position: relative;
}
@media (max-width: 56.25em) {
  .header {
    padding-top: 0;
  }
}
.header__logo img {
  width: 4.125rem;
  height: 2.5625rem;
}
@media (max-width: 56.25em) {
  .header__logo img {
    width: 2.9375rem;
    height: 1.875rem;
  }
}
@media (max-width: 56.25em) {
  .header__logo {
    position: absolute;
    top: 0;
    left: 0;
    translate: 100% 180%;
  }
}
.header__nav {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  align-self: flex-start;
}
.header__nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__nav ul li {
  list-style-type: none;
  padding: 20px;
}
.header__nav ul li a {
  text-decoration: none;
  font-size: 1rem;
  color: var(--Very-dark-blue);
  font-weight: 700;
}
@media (max-width: 56.25em) {
  .header__nav ul {
    display: none;
    height: 100vh;
    flex-direction: column;
    align-items: flex-start;
    background: #fff;
    width: 70%;
    margin-top: 0;
    position: absolute;
  }
}
.header__hamburger {
  position: absolute;
  top: 0;
  right: 0;
  translate: -100% 140%;
}
.header__hamburger img {
  display: none;
  width: 2.0625rem;
  height: 2.0625rem;
  cursor: pointer;
}
@media (max-width: 56.25em) {
  .header__hamburger img {
    display: inline-block;
  }
}

.wrapper {
  padding: 0 10.3125rem;
}
@media (max-width: 56.25em) {
  .wrapper {
    padding: 0;
  }
}